import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, NavLink as ReactLink, useNavigate } from "react-router-dom";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { logoutUserReducer } from "../reducers/authReducer";

const NavBarComponent = (args) => {
  const navigate = useNavigate();
  const { userdetails } = useSelector((store) => store.user);
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  const logoutHandler = async () => {
    await dispatch(logoutUserReducer());
    navigate("/login");
  };
  return (
    <>
      <Navbar {...args}>
        <NavbarBrand tag={ReactLink} to="/">
          SP.
        </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ms-auto" navbar>
            <NavItem>
              <NavLink tag={ReactLink} to="/home">
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/portfolios">
                Portfolio
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/blog">
                Blog
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/components">
                Components
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/contact">
                Contact
              </NavLink>
            </NavItem>
            <NavItem>
              {userdetails.token ? (
                <ul className="px-0">
                  <UncontrolledDropdown nav inNavbar>
                    <DropdownToggle nav>
                      <img
                        src="/images/ux-design.jpg"
                        alt="UI/UX Design"
                        style={{ height: "25px", width: "25px" }}
                      />
                    </DropdownToggle>
                    <DropdownMenu end className="mt-md-2">
                      <DropdownItem>
                        <div onClick={(e) => navigate("/profile")}>
                          My Profile
                        </div>
                      </DropdownItem>
                      {/* <DropdownItem>Option 2</DropdownItem> */}
                      <DropdownItem divider />
                      <DropdownItem>
                        <div onClick={logoutHandler}>Logout</div>
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </ul>
              ) : (
                <NavLink tag={ReactLink} to="/login">
                  Login
                </NavLink>
              )}
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </>
  );
};

export default NavBarComponent;
