import React from "react";
import { useDispatch } from "react-redux";
import { UncontrolledAlert } from "reactstrap";
import { clearError, clearSuccess } from "../reducers/authReducer";

const ErrorAlert = (props) => {
  const dispatch = useDispatch();
  const onDismiss = async () => {
    await dispatch(clearError());
    console.log("error closed");
  };

  return (
    <div className="alert-wrapper">
      <UncontrolledAlert color="danger" onClick={onDismiss}>
        {props.message}
      </UncontrolledAlert>
    </div>
  );
};
const SuccessAlert = (props) => {
  const dispatch = useDispatch();
  const onDismiss = async () => {
    await dispatch(clearSuccess());
    console.log("success closed");
  };

  return (
    <div className="alert-wrapper">
      <UncontrolledAlert color="primary" onClick={onDismiss}>
        {props.message}
      </UncontrolledAlert>
    </div>
  );
};

export { ErrorAlert, SuccessAlert };
